@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

html {
  scroll-behavior: smooth;
}

body {
  @apply overflow-x-hidden overflow-y-auto bg-body text-black font-poppins scrollbar-hide w-screen scrollbar-track-warning scrollbar-thumb-black min-h-screen relative;
}

[x-cloak] {
  display: none;
}

.dot-gray {
  @apply rounded-full h-1 w-1 bg-gray-500;
}

.dot-warning {
  @apply rounded-full h-1 w-1 bg-warning;
}

.dot-danger {
  @apply rounded-full h-1 w-1 bg-danger;
}

.dot-black {
  @apply rounded-full h-1 w-1 bg-black;
}

.btn-rounded {
  @apply rounded-xl;
}

.live_notify_list {
  @apply py-2 w-full flex-grow relative flex-none overflow-y-scroll space-y-4 break-words scrollbar-thumb-warning scrollbar-track-gray-700 scrollbar-hide;
}

.unread {
  @apply overflow-hidden text-gray-700 relative bg-gray-300 font-bold rounded-xl px-2 pt-2 pb-6 space-y-2 duration-500 cursor-pointer hover:shadow-lg break-words flex-grow flex-none;
}

.time {
  @apply text-s absolute right-3 bottom-1 font-bold;
}

.read {
  @apply text-gray-700 relative bg-gray-300 font-bold rounded-xl px-2 pt-2 pb-6 flex items-start space-x-2;
}
